//this file will contain the component that will be used to display the root content table metadata
import {AiFillEyeInvisible, AiFillEye} from "react-icons/ai";
import $ from 'jquery';

export default function RocrateMetadataTable(props: any) {
    const loading = props.loading;
    const data = props.data;

    //function that will show or hide the metadata table and change the icon id for table is rocrate_metadata_table , id for button is eyebutton
    const showHideMetadata = () => {
        if ($("#rocrate_metadata_table").css("display") == "none") {
            $("#rocrate_metadata_table").css("display", "table");
            $("#eyebutton").html("Hide");
        }
        else {
            $("#rocrate_metadata_table").css("display", "none");
            $("#eyebutton").html("Show");
        }
    }


    return (
        //if the data is empty show a message
        loading ? <></> :
        //else loop over the data and show it
        <>
        <div className="flex_row">
            <h4>Core RO-Crate Metadata </h4>
            <button className="file_menu_button" id="eyebutton" onClick={() => showHideMetadata()}>Hide</button>
        </div>
        <table id="rocrate_metadata_table">
            <tr>
                <th>Attribute</th>
                <th>Value</th>
            </tr>
            {
                Object.keys(data).map((key) => {
                    let key_to_show = key.replace("rocrate_", "");

                    //if data[key] is None then give tr class error_row
                    if (data[key] == "None") {
                        return (
                            <tr className="error_row">
                                <td><b>{key_to_show}</b></td>
                                <td>{data[key]}</td>
                            </tr>
                        )
                    }
                    //else give tr class normal_row
                    return (
                        <tr>
                            <td>{key_to_show}</td>
                            <td>{data[key]}</td>
                        </tr>
                    )
                })
            }
        </table>
        </>
    )
}

